'use client';

import { Button, Table, type TableColumnType, Tag, message } from 'antd';
import FundChange from './FundChange';
import Link from 'next/link';
import { useState } from 'react';
import FundCreateModal from './FundCreateModal';
const ETFList = ({ dataSource }: { dataSource: any[] }) => {
  const [editModalOpen, setEditModalOpen] = useState(false);
  const [editModalData, setEditModalData] = useState<any>(null);

  const handleEditOk = (values: any) => {
    fetch('/api/pool', {
      method: 'POST',
      body: JSON.stringify(values),
      headers: {
        'Content-Type': 'application/json',
      },
    })
      .then(res => res.json())
      .then(data => {
        if (data.error) {
          message.error(data.error);
        } else {
          message.success('新增成功');
          setEditModalOpen(false);
          setEditModalData(null);
        }
      });
  };

  const columns: TableColumnType<any>[] = [
    {
      title: 'ETF代码',
      dataIndex: 'code',
      key: 'code',
    },
    {
      title: 'ETF名称',
      dataIndex: 'name',
      key: 'name',
      render: (value: string, record: any) => (
        <Link
          href={`/pool/${record.code}`}
          target="_blank"
          className="text-primary hover:text-blue-700 font-medium font-bold"
        >
          {value}
        </Link>
      ),
    },
    {
      title: '类别',
      dataIndex: 'category',
      key: 'category',
      render: (value: string, record: any) => <Tag color="blue">{value}</Tag>,
    },
    {
      title: '最新价',
      dataIndex: 'price',
      key: 'price',
      render: (value: string, record: any) =>
        value ? parseFloat(value).toFixed(2) : '-',
    },
    {
      title: '涨跌幅',
      dataIndex: 'change',
      key: 'change',
      render: (value: string, record: any) => <FundChange value={value} />,
    },
    {
      title: '操作',
      dataIndex: 'action',
      key: 'action',
      render: (text: string, record: any) => (
        <Button
          type="link"
          onClick={() => setEditModalOpen(true)}
          className="text-primary hover:text-blue-700 font-medium font-bold"
        >
          编辑
        </Button>
      ),
    },
  ];

  return (
    <div className="bg-white rounded-lg shadow-card">
      <Table dataSource={dataSource} columns={columns} />
      <FundCreateModal
        open={editModalOpen}
        onCancel={() => setEditModalOpen(false)}
        onOk={handleEditOk}
      />
    </div>
  );
};

export default ETFList;
