import { Modal, Form, Input, Select } from 'antd';

interface FundCreateModalProps {
  open: boolean;
  onCancel: () => void;
  onOk: (values: any) => void;
}

const FundCreateModal = ({ open, onCancel, onOk }: FundCreateModalProps) => {
  const [form] = Form.useForm();

  const handleOk = () => {
    form.validateFields().then(values => {
      onOk(values);
    });
  };

  return (
    <Modal title="新增ETF" open={open} onCancel={onCancel} onOk={handleOk}>
      <Form form={form}>
        <Form.Item label="ETF代码" name="code">
          <Input />
        </Form.Item>
        <Form.Item label="ETF名称" name="name">
          <Input />
        </Form.Item>
        <Form.Item label="类别" name="category">
          <Select
            options={[
              { label: '股票', value: 'stock' },
              { label: '债券', value: 'bond' },
              { label: '混合', value: 'mixed' },
            ]}
          />
        </Form.Item>
      </Form>
    </Modal>
  );
};

export default FundCreateModal;
