'use client';
import ContentHeader from '@/components/ContentHeader';
import { Button } from 'antd';
import ETFList from '@/components/ETFList';
import { useState, useEffect } from 'react';

const PoolPage = () => {
  const [etfs, setETFs] = useState<any[]>([]);
  useEffect(() => {
    fetchETFs();
    fetchETFPrice('510300').then(data => {
      console.log(data);
    });
  }, []);

  const fetchETFs = async () => {
    const data = (await fetch('/api/pool').then(res => res.json())) as any[];
    setETFs(data);
  };

  const fetchETFPrice = async (code: string) => {
    const data = (await fetch(`/api/pool/price?code=${code}`).then(res =>
      res.json()
    )) as any;
    return data;
  };

  return (
    <div>
      <div className="mb-6">
        <ContentHeader
          title="ETF池子"
          description="管理您的ETF投资标的池子"
          action={
            <Button type="primary" icon={<i className="fas fa-plus"></i>}>
              新增ETF
            </Button>
          }
        />
      </div>
      <div>
        <ETFList dataSource={etfs} />
      </div>
    </div>
  );
};

export default PoolPage;
